.elevation-table {
  &__table {
    overflow-x: hidden; //TODO fix
    //overflow-y: auto;
    //max-height: inherit;
    //display: block;
    //border-color: transparent;

    height: 100%;
    overflow-y: scroll;
    display: block;
  }

  &__caption {
    margin-bottom: 2px;
    font-size: 14px;
    color: rgb(234, 236, 239);
    text-align: left;
    padding: 0 4px;
  }

  &__head {
    position: sticky;
    top: 0;
  }

  &__head-cell {
    font-size: 11px;
    line-height: 1.6;
    text-align: left;
    padding: 4px 8px;
    color: rgb(156, 167, 182);
  }

  &__row {
    font-weight: 400;
    line-height: 1.5;
    color: rgb(234, 236, 239);
    background: #141826;

    &:hover {
      background: #384055;
      cursor: pointer;
    }
  }

  &__cell {
    color: #fafafa;
    line-height: 1.6;
    padding: 4px 8px;
    font-size: 11px;

    &--default {
      color: #fafafa;
    }

    &--red {
      color: rgb(246, 70, 93);
    }

    &--green {
      color: rgb(14, 203, 129);
    }
  }
}
