.trading {
  &__wrapper-container {
    width: 100%;
    height: 100vh;
    background-color: #151927;
  }

  width: 1920px;
  max-width: 100%;
  padding: 20px;
  box-sizing: border-box;
  background-color: #141826;

  height: calc(100vh - 130px);
  min-height: 895px;

  display: flex;

  &__symbols {
    //height: 100%;
    //overflow-y: auto;

    //max-height: 100%;
  }

  &__chart {
    height: 570px;
    max-width: 100%;
  }

  &__order-book {
    display: flex;
    flex-direction: column;
  }

  > * {
    margin: 8px;
  }

  &__wrapper {
    display: flex;
    flex: auto;
    flex-direction: column;
  }
  &__wrapper-group {
    display: flex;
    margin-top: 16px;
    gap: 16px;
  }
  &__wrapper-group-sidebar {
    display: flex;
    gap: 16px;
  }

  &__trade {
    max-width: 100%;
  }
}
