.profile-sidebar {
  width: 200px;
  flex-shrink: 0;
  box-sizing: border-box;
  padding: 0.5rem;
  border: 1px solid #ebedf0;
  border-radius: 2px;
  background-color: #fafafa;

  @media (max-width: 768px) {
    width: 100%;
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
