@import 'styles/colors';
@import 'styles/mixins';

.profile {
  padding-top: 18px;

  @media (max-width: 768px) {
    padding-top: 2px;
  }

  &__container {
    display: flex;
    gap: 8px;
    @include container();

    @media (max-width: 768px) {
      flex-direction: column;
    }
  }

  &__wrapper {
    width: 100%;
  }
}
