@import 'styles/colors';

.user-profile {
  width: 100%;
  padding: 1rem;
  border: 1px solid #edf0f7;
  box-sizing: border-box;

  &__caption {
    padding: 5px 1.25rem;
  }

  &__head-cell {
    padding: 2px 1.25rem;
  }

  &__cell {
    padding: 2px 1.25rem;
  }

  &__table {
    margin-top: 20px;
  }

  &__info {
    display: flex;
    justify-content: flex-start;
  }

  &__text {
    &-username {
      font-size: 22px;
      font-weight: bold;
      padding-right: 8px;
    }

    &-id {
      font-size: 12px;
      color: $Black80;
      padding-right: 5px;
    }
  }

  &__wrapper {
    display: flex;
    align-items: center;

    & > div {
      padding-right: 10px;
    }
  }

  &__mark-status {
    font-weight: bold;
    text-transform: uppercase;
    padding: 0 5px;
  }

  &__btn-submit {
    max-width: 200px;
    margin-right: 10px;
  }

  &__image-container {
    display: flex;
    justify-content: left;
    margin-top: 10px;

    img {
      width: 460px;
      margin-right: 5px;
      border: 1px solid #9b9797;
      border-radius: 2px;
    }
  }

  &__docs-container {
    margin-top: 10px;
    font-size: 16px;
  }
}
