.dialog-modal {
  &__modal {
    width: 100%;
    max-width: 340px;

    &-header {
      text-align: right;
    }
  }

  &__cross-icon {
    cursor: pointer;
    width: 18px;
  }

  &__btn-group {
    margin-top: 20px;
    display: flex;
    justify-content: space-around;

    & button {
      margin: 0 5px;
    }
  }
}
