@import 'styles/colors';

.elevation {
  ///* width */
  //::-webkit-scrollbar {
  //  width: 12px;
  //}
  //
  ///* Track */
  //::-webkit-scrollbar-track {
  //  background: rgba(0, 0, 0, 0.08);
  //}
  //
  ///* Handle */
  //::-webkit-scrollbar-thumb {
  //  background: rgba(166, 162, 162, 0.2);
  //}
  //
  ///* Handle on hover */
  //::-webkit-scrollbar-thumb:hover {
  //  background: rgba(166, 162, 162, 0.28);
  //}

  width: 100%;
  max-width: 270px;
  background-color: rgb(15, 18, 31);
  border-radius: 4px;
  padding: 16px;
  box-sizing: border-box;
  box-shadow: 0 0 3px -1px #d7d8cb, 0 10px 25px -15px #efe6e6, 0 0 0 1px rgb(4, 14, 44);

  //max-height: inherit;

  &__order-book {
  }

  &__btn {
    box-sizing: border-box;
    position: relative;
    -webkit-user-select: none;
    -moz-user-select: none;
    -ms-user-select: none;
    user-select: none;
    cursor: pointer;
    outline: 0;
    border: none;
    -webkit-tap-highlight-color: transparent;
    display: inline-block;
    white-space: nowrap;
    text-decoration: none;
    vertical-align: baseline;
    text-align: center;
    margin-top: 12px;
    min-width: 64px;
    width: 100%;
    line-height: 36px;
    padding: 0 16px;
    border-radius: 4px;
    overflow: visible;
    transform: translate3d(0, 0, 0);
    transition: background 0.4s cubic-bezier(0.25, 0.8, 0.25, 1), box-shadow 280ms cubic-bezier(0.4, 0, 0.2, 1);
    color: white;
    background-color: #343475;

    &:hover {
      background-color: #282864ff;
    }

    &:disabled {
      cursor: no-drop;
      opacity: 0.5;
    }
  }

  &__input {
    background-color: #0f121f;
    border: 1px solid $White;
    color: $White;
  }

  &__form {
    color: $White;

    &-header {
      text-align: center;

      &-label {
        font-size: 14px;
      }

      &-value {
        font-size: 16px;
        margin-left: 8px;
        font-weight: bold;
      }
    }
  }

  &__price {
    background-color: rgb(20, 24, 38);
    color: white;
    border: 1px solid #293047;
    margin: 10px 0 8px 0;
    justify-content: space-evenly;
    padding: 6px 0;
    font-size: 16px;
    border-radius: 4px;
    text-align: center;
  }

  &__balance-value {
    margin-right: 8px;
  }
}
